.call-phone {
    /*background: lawngreen;*/


    cursor: pointer;
    width: 150px;
    height: 150px;

}
.call-phone__circle {
    width: 160px;
    height: 160px;
    top: 20px;
    left: 20px;
    margin: 0;
    position: absolute;
    background-color: transparent;
    border-radius: 100%;
    border: 2px solid rgba(244,67,54,0.4);
    -webkit-animation: call-phone__circle-anim 1.2s infinite ease-in-out;
            animation: call-phone__circle-anim 1.2s infinite ease-in-out;
    transition: all 0.5s;
    vertical-align: baseline;
}

@-webkit-keyframes call-phone__circle-anim{
    0%{-webkit-transform:rotate(0deg) scale(0.5) skew(1deg);transform:rotate(0deg) scale(0.5) skew(1deg);
        opacity:.1  }
    30%{-webkit-transform:rotate(0deg) scale(0.7) skew(1deg);transform:rotate(0deg) scale(0.7) skew(1deg);
        opacity:.5  }
    100%{-webkit-transform:rotate(0deg) scale(1) skew(1deg);transform:rotate(0deg) scale(1) skew(1deg);
        opacity:.1  }
}

@keyframes call-phone__circle-anim{
    0%{-webkit-transform:rotate(0deg) scale(0.5) skew(1deg);transform:rotate(0deg) scale(0.5) skew(1deg);
        opacity:.1  }
    30%{-webkit-transform:rotate(0deg) scale(0.7) skew(1deg);transform:rotate(0deg) scale(0.7) skew(1deg);
        opacity:.5  }
    100%{-webkit-transform:rotate(0deg) scale(1) skew(1deg);transform:rotate(0deg) scale(1) skew(1deg);
        opacity:.1  }
}
.call-phone__circle-fill {
    background-color: rgba(244,67,54,1);
    opacity: 0.75;
    width: 100px;
    height: 100px;
    top: 50px;
    left: 50px;
    position: absolute;
    border-radius: 100%;
    opacity: 0.1;
    -webkit-animation: call-phone__circle-fill-anim 2.3s infinite ease-in-out;
            animation: call-phone__circle-fill-anim 2.3s infinite ease-in-out;
    transition: all 0.5s;
}

@-webkit-keyframes call-phone__circle-fill-anim{
    0%{-webkit-transform:rotate(0deg) scale(0.7) skew(1deg);transform:rotate(0deg) scale(0.7) skew(1deg);
        opacity:.2  }
    50%{-webkit-transform:rotate(0deg) scale(1) skew(1deg);transform:rotate(0deg) scale(1) skew(1deg);
        opacity:.2  }
    100%{-webkit-transform:rotate(0deg) scale(0.7) skew(1deg);transform:rotate(0deg) scale(0.7) skew(1deg);opacity:.2  }
}

@keyframes call-phone__circle-fill-anim{
    0%{-webkit-transform:rotate(0deg) scale(0.7) skew(1deg);transform:rotate(0deg) scale(0.7) skew(1deg);
        opacity:.2  }
    50%{-webkit-transform:rotate(0deg) scale(1) skew(1deg);transform:rotate(0deg) scale(1) skew(1deg);
        opacity:.2  }
    100%{-webkit-transform:rotate(0deg) scale(0.7) skew(1deg);transform:rotate(0deg) scale(0.7) skew(1deg);opacity:.2  }
}
.call-phone__circle-img {
    position: relative;
    background-color: rgba(244,67,54,0.1);
    width: 60px;
    height: 60px;
    top: 65px;
    left: 65px;
    position: absolute;
    border: 5px solid #f44336;
    border-radius: 50%;
    border-radius: 100%;
    opacity: 0.7;
    -webkit-animation: call-phone__circle-img-anim 1s infinite ease-in-out;
            animation: call-phone__circle-img-anim 1s infinite ease-in-out;
}
@-webkit-keyframes call-phone__circle-img-anim{
    0%{-webkit-transform:rotate(0deg) scale(1) skew(1deg);transform:rotate(0deg) scale(1) skew(1deg)  }
    10%{-webkit-transform:rotate(-25deg) scale(1) skew(1deg);transform:rotate(-25deg) scale(1) skew(1deg)  }
    20%{-webkit-transform:rotate(25deg) scale(1) skew(1deg);transform:rotate(25deg) scale(1) skew(1deg)  }
    30%{-webkit-transform:rotate(-25deg) scale(1) skew(1deg);transform:rotate(-25deg) scale(1) skew(1deg)  }
    40%{-webkit-transform:rotate(25deg) scale(1) skew(1deg);transform:rotate(25deg) scale(1) skew(1deg)  }
    100%,50%{-webkit-transform:rotate(0deg) scale(1) skew(1deg);transform:rotate(0deg) scale(1) skew(1deg)  }
}
@keyframes call-phone__circle-img-anim{
    0%{-webkit-transform:rotate(0deg) scale(1) skew(1deg);transform:rotate(0deg) scale(1) skew(1deg)  }
    10%{-webkit-transform:rotate(-25deg) scale(1) skew(1deg);transform:rotate(-25deg) scale(1) skew(1deg)  }
    20%{-webkit-transform:rotate(25deg) scale(1) skew(1deg);transform:rotate(25deg) scale(1) skew(1deg)  }
    30%{-webkit-transform:rotate(-25deg) scale(1) skew(1deg);transform:rotate(-25deg) scale(1) skew(1deg)  }
    40%{-webkit-transform:rotate(25deg) scale(1) skew(1deg);transform:rotate(25deg) scale(1) skew(1deg)  }
    100%,50%{-webkit-transform:rotate(0deg) scale(1) skew(1deg);transform:rotate(0deg) scale(1) skew(1deg)  }
}

